import axios from "axios";
import React, { useEffect, useState } from "react";
import { MdSearch } from "react-icons/md";
import { connect } from "react-redux";
import { ClaimTypesInterface } from ".";
import { PushPopupAlert } from "../../../actions";
import { Auth } from "../../../actions/auth";
import { PopupAlertData } from "../../../components/Alert/Popup";
import { GetApplicationsAPI } from "../../../interfaces/application";
import { PopupAlertTheme } from "../../../interfaces/popupAlert";
import { StoreState } from "../../../reducers";
import { DocumentReducerInterface } from "../../../reducers/document";
import { capitalizeFirstLetter } from "../../../shared/dataTest";
import { API } from "../../../utils/api";
import { errorToText } from "../../../utils/errors";

type ClaimFormProps = {
  auth: Auth;
  documentReducer: DocumentReducerInterface;
  PushPopupAlert: (alert: PopupAlertData) => void;
};

const ClaimForm: React.FC<ClaimFormProps> = (props) => {
  const [loadingApplications, setLoadindApplications] =
    useState<boolean>(false);
  const [loadingForm, setLoadindForm] = useState<boolean>(false);
  const [formError, setFormError] = useState<{
    element: string;
    msg: string;
  } | null>(null);
  const [thisClaimType, setThisClaimType] = useState<string>("");
  const [claimComment, setClaimComment] = useState<string>("");
  const [claimFile, setClaimFile] = useState<string>("");
  const [chooseClaimFile, setChooseClaimFile] = useState<boolean>(false);
  const [applications, setApplications] = useState<GetApplicationsAPI[] | null>(
    null
  );
  const [rejectedApplications, setRejectedApplications] = useState<
    GetApplicationsAPI[] | null
  >(null);

  useEffect(() => {
    const LoadApplications = async () => {
      try {
        const res = await axios.get<GetApplicationsAPI[]>(
          `${API.APPLICATION}/application/${props.auth.user.user_id}`
        );
        if (res.status === 200) {
          setApplications(res.data);
          if (res.data !== null && res.data.length > 0) {
            let dataRejected: GetApplicationsAPI[] = [];
            for (const key in res.data) {
              if (Object.prototype.hasOwnProperty.call(res.data, key)) {
                if (res.data[key].application_status === "rejected") {
                  dataRejected.push(res.data[key]);
                }
              }
            }
            setRejectedApplications(dataRejected);
          }
        }
      } catch (error) {
        console.error(error);
        props.PushPopupAlert({
          title: errorToText(error),
          theme: PopupAlertTheme.danger,
        });
      }
      setLoadindApplications(false);
    };

    if (rejectedApplications === null) {
      setLoadindApplications(true);
      LoadApplications();
    }
  }, [rejectedApplications, props]);

  const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {};

  return (
    <form onSubmit={(e) => handleSubmit(e)}>
      {console.log({ docs: props.auth.user.user_id, rejectedApplications })}
      <div className="divide-y divide-gray-200 px-6">
        <div className="my-4 text-base text-gray-700 sm:text-lg">
          <div className="flex flex-col mb-2">
            <label>Claim type</label>
            <select
              className={`rounded px-2 py-2 w-full bg-white focus:outline-none ${
                formError && formError.element === "claim_type"
                  ? "border-red-500"
                  : "border-gray-500"
              } border-2`}
              onChange={(e: React.ChangeEvent<HTMLSelectElement>) => {
                setThisClaimType(e.target.value);
                formError &&
                  formError.element === "claim_type" &&
                  setFormError(null);
              }}
              value={thisClaimType}
              disabled={loadingForm}
            >
              <option value="">Choose claim type</option>
              <option value={ClaimTypesInterface.DOCUMENT}>
                {capitalizeFirstLetter(ClaimTypesInterface.DOCUMENT)}
              </option>
              <option value={ClaimTypesInterface.APPLICATION}>
                {capitalizeFirstLetter(ClaimTypesInterface.APPLICATION)}
              </option>
              <option value={ClaimTypesInterface.PLACEMENT}>
                {capitalizeFirstLetter(ClaimTypesInterface.PLACEMENT)}
              </option>
            </select>
            {formError && formError.element === "claim_type" && (
              <span className="text-red-500">{formError.msg}</span>
            )}
          </div>
          <div className="flex flex-col mb-2">
            <label>
              Which {thisClaimType.length > 0 ? thisClaimType : "file"} are you
              claiming on?
            </label>
            <div
              className={`text-left bg-white border-2 border-gray-700 rounded w-full ${
                chooseClaimFile ? "h-64" : "h-10"
              } ${
                formError && formError.element === "claim_file"
                  ? "border-red-500"
                  : "border-gray-500"
              } border-2 ${
                thisClaimType.length > 0 && !chooseClaimFile
                  ? "cursor-pointer"
                  : !chooseClaimFile && "cursor-not-allowed"
              }`}
              title={
                thisClaimType.length <= 0 ? "Choose claim type first!" : ""
              }
              onClick={() => {
                thisClaimType.length > 0 && setChooseClaimFile(true);
                formError &&
                  formError.element === "claim_file" &&
                  setFormError(null);
              }}
            >
              {claimFile.length > 0 && !chooseClaimFile
                ? claimFile
                : !chooseClaimFile && (
                    <p className="my-1 mx-2">Select Claim file</p>
                  )}

              {chooseClaimFile && (
                <section>
                  {/* SEARCH PART */}
                  <div className="flex items-center w-full my-1">
                    <MdSearch className="w-6 h-6 absolute ml-2" />
                    <input
                      type="text"
                      placeholder="Search"
                      className="focus:border-2 h-7 ml-1 pl-9 mr-2 bg-gray-100 focus:border-gray-700 w-full rounded"
                    />
                    <button
                      className="bg-red-200 text-red-700 py-1 px-2 mr-1 rounded text-sm focus:outline-none"
                      type="button"
                      onClick={() => setChooseClaimFile(false)}
                    >
                      Cancel
                    </button>
                  </div>
                  {/* END SEARCH PART ///////////////// */}
                  {console.log({ chooseClaimFile })}

                  <div className="w-full h-52 mt-2 overflow-y-auto divide-y divide-gray-300">
                    <div className="flex items-center justify-between my-1 hover:bg-gray-200">
                      <div className="p-1 w-full mr-2 cursor-pointer">
                        <p className="text-base font-medium text-black">
                          Document One
                        </p>
                      </div>
                      <button
                        type="button"
                        className="bg-blue-200 hover:bg-blue-300 text-blue-700 hover:text-blue-800 py-1 px-2 mr-1 rounded text-sm"
                        onClick={() => setChooseClaimFile(false)}
                      >
                        View
                      </button>
                    </div>
                    <div className="flex items-center justify-between cursor-pointer my-1 hover:bg-gray-200">
                      <div className="p-1">
                        <p className="text-base font-medium text-black">
                          Document One
                        </p>
                      </div>
                      <button className="bg-blue-200 text-blue-700 py-1 px-2 mr-1 rounded text-sm">
                        View
                      </button>
                    </div>
                    <div className="flex items-center justify-between cursor-pointer my-1 hover:bg-gray-200">
                      <div className="p-1">
                        <p className="text-base font-medium text-black">
                          Document One
                        </p>
                      </div>
                      <button className="bg-blue-200 text-blue-700 py-1 px-2 mr-1 rounded text-sm">
                        View
                      </button>
                    </div>
                    <div className="flex items-center justify-between cursor-pointer my-1 hover:bg-gray-200">
                      <div className="p-1">
                        <p className="text-base font-medium text-black">
                          Document One
                        </p>
                      </div>
                      <button className="bg-blue-200 text-blue-700 py-1 px-2 mr-1 rounded text-sm">
                        View
                      </button>
                    </div>
                    <div className="flex items-center justify-between cursor-pointer my-1 hover:bg-gray-200">
                      <div className="p-1">
                        <p className="text-base font-medium text-black">
                          Document One
                        </p>
                      </div>
                      <button className="bg-blue-200 text-blue-700 py-1 px-2 mr-1 rounded text-sm">
                        View
                      </button>
                    </div>
                    <div className="flex items-center justify-between cursor-pointer my-1 hover:bg-gray-200">
                      <div className="p-1">
                        <p className="text-base font-medium text-black">
                          Document One
                        </p>
                      </div>
                      <button className="bg-blue-200 text-blue-700 py-1 px-2 mr-1 rounded text-sm">
                        View
                      </button>
                    </div>
                    <div className="flex items-center justify-between cursor-pointer my-1 hover:bg-gray-200">
                      <div className="p-1">
                        <p className="text-base font-medium text-black">
                          Document One
                        </p>
                      </div>
                      <button className="bg-blue-200 text-blue-700 py-1 px-2 mr-1 rounded text-sm">
                        View
                      </button>
                    </div>
                    <div className="flex items-center justify-between cursor-pointer my-1 hover:bg-gray-200">
                      <div className="p-1">
                        <p className="text-base font-medium text-black">
                          Document One
                        </p>
                      </div>
                      <button className="bg-blue-200 text-blue-700 py-1 px-2 mr-1 rounded text-sm">
                        View
                      </button>
                    </div>
                    <div className="flex items-center justify-between cursor-pointer my-1 hover:bg-gray-200">
                      <div className="p-1">
                        <p className="text-base font-medium text-black">
                          Document One
                        </p>
                      </div>
                      <button className="bg-blue-200 text-blue-700 py-1 px-2 mr-1 rounded text-sm">
                        View
                      </button>
                    </div>
                    <div className="flex items-center justify-between cursor-pointer my-1 hover:bg-gray-200">
                      <div className="p-1">
                        <p className="text-base font-medium text-black">
                          Document One
                        </p>
                      </div>
                      <button className="bg-blue-200 text-blue-700 py-1 px-2 mr-1 rounded text-sm">
                        View
                      </button>
                    </div>
                    <div className="flex items-center justify-between cursor-pointer my-1 hover:bg-gray-200">
                      <div className="p-1">
                        <p className="text-base font-medium text-black">
                          Document One
                        </p>
                      </div>
                      <button className="bg-blue-200 text-blue-700 py-1 px-2 mr-1 rounded text-sm">
                        View
                      </button>
                    </div>
                  </div>
                </section>
              )}
            </div>
            {formError && formError.element === "claim_file" && (
              <span className="text-red-500">{formError.msg}</span>
            )}
          </div>
          <div className="flex flex-col">
            <label>Comment</label>
            <textarea
              className={`text-left input-white w-20 inline-block ${
                formError && formError.element === "claim_comment"
                  ? "border-red-500"
                  : "border-gray-500"
              } border-2`}
              placeholder={`Claim comment`}
              style={{ resize: "none" }}
              value={claimComment}
              onChange={(e) => {
                setClaimComment(e.target.value);
                formError &&
                  formError.element === "claim_comment" &&
                  setFormError(null);
              }}
              rows={3}
              disabled={loadingForm}
            ></textarea>
            {formError && formError.element === "claim_comment" && (
              <span className="text-red-500">{formError.msg}</span>
            )}
          </div>
        </div>
        <div className="pt-4 flex justify-between">
          <button
            // onClick={() => {
            //   setActionType("");
            //   setDocFieldsId("");
            //   setDocFieldsTitle("");
            //   setDocFieldsDescription("");
            // }}
            className={`btn bg-gray-400 text-white hover:bg-gray-600 focus:outline-none ${
              loadingForm && "cursor-not-allowed"
            }`}
            type="button"
            disabled={loadingForm}
          >
            Exit
          </button>
          <button
            className={`btn bg-primary text-white hover:bg-blue-600 focus:outline-none ${
              loadingForm && "cursor-wait"
            }`}
            type="submit"
            disabled={loadingForm}
          >
            {loadingForm ? "Loading..." : "Claim"}
          </button>
        </div>
      </div>
    </form>
  );
};

const mapStateToProps = ({
  auth,
  document,
}: StoreState): {
  auth: Auth;
  documentReducer: DocumentReducerInterface;
} => {
  return {
    auth: auth,
    documentReducer: document,
  };
};

export default connect(mapStateToProps, { PushPopupAlert })(ClaimForm);
